import Scroll from "../../../base/scroll/scroll.vue"
import Step from "../../../base/step/step.vue"
import Upload from "../../../base/upload/upload.vue"

export default {
  name: "getPrice2",
  components: {
    Step,
    Upload,
    Scroll
  },
  data() {
    return {
      stepArr: ["核对商品", "客服聊天", "填写订单信息"],
      stepIndex: 1,
      isOk: true, //按钮可点击
      chatImg: [], //客服聊天截图
    }
  },
  methods: {
    doNext() {
      if (this.chatImg.length === 0) {
        this.$vux.alert.show({
          title: '提示',
          content: '请上传完整截图',
        });
        return false
      }
      let obj = {
        chatImg: this.chatImg
      }
      this.$router.push({ name: 'getPriceThreeStep3', query: { buyerTaskRecordId: this.$route.query.buyerTaskRecordId, obj: obj } })
    }
  }
}



